import PropTypes from 'prop-types';
import { useContext } from 'react';

// theme
import ThemeConfig from './mui-theme/theme';
import GlobalStyles from './mui-theme/theme/globalStyles';

// components
import RtlLayout from './mui-theme/components/RtlLayout';
import NotistackProvider from './mui-theme/components/NotistackProvider';
import ThemePrimaryColor from './mui-theme/components/ThemePrimaryColor';
import ThemeLocalization from './mui-theme/components/ThemeLocalization';
import NewsComponent from './components/NewsComponent';

// import { 
      // "@minimal/minimal-kit-react": "file:../../../../../mui-theme-library",

//   RtlLayout, 
//   NotistackProvider, 
//   ThemePrimaryColor, 
//   ThemeLocalization  } from '@minimal/minimal-kit-react/src/components';

import { Card, Container } from '@mui/material';

import { AuthenticatedView, UnauthenticatedView } from 'auth/KeycloakViews';
import { useTranslation } from 'react-i18next';
import KeycloakContext from 'auth/KeycloakContext';

// ----------------------------------------------------------------------
App.propTypes = {
  contentTypeCode: PropTypes.string.isRequired,
  categoryCode: PropTypes.string.isRequired,
  onError: PropTypes.func,
  serviceUrl: PropTypes.string,
};

App.defaultProps = {
  onError: () => {},
  serviceUrl: '',
};

export default function App(props) {
  const { t } = useTranslation();

  const keycloak = useContext(KeycloakContext);
  return (
    <ThemeConfig>
      <ThemePrimaryColor>
        <ThemeLocalization>
          <RtlLayout>
            <NotistackProvider>
              <GlobalStyles />
              <Container maxWidth={'md'}>
              <UnauthenticatedView keycloak={keycloak}>
                {t('common.notAuthenticated')}
              </UnauthenticatedView>
              <AuthenticatedView keycloak={keycloak}>
                <Card>
                  <NewsComponent {...props}/>
                </Card>
              </AuthenticatedView>
              </Container>
            </NotistackProvider>
          </RtlLayout>
        </ThemeLocalization>
      </ThemePrimaryColor>
    </ThemeConfig>
  );
}


