import PropTypes from 'prop-types';
// material
import { Box, Stack, Link, Card, Typography, CardHeader } from '@mui/material';
// utils
import { fToNow } from './../mui-theme/utils/formatTime';
import mockData from './../mui-theme/utils/mock-data';
//
import Scrollbar from './../mui-theme/components/Scrollbar';
import { useEffect, useState } from 'react';

import { apiConferenceGet } from 'api/newsContent';

import { useTranslation } from 'react-i18next';

// ----------------------------------------------------------------------

const MOCK_NEWS = [...Array(5)].map((_, index) => ({
  id: mockData.id(index),
  title: mockData.text.title(index),
  description: mockData.text.description(index),
  image: mockData.image.cover(index),
  postedAt: mockData.time(index)
}));


// ----------------------------------------------------------------------

NewsItem.propTypes = {
  news: PropTypes.shape({
    id: PropTypes.string,
    description: PropTypes.string,
    image: PropTypes.string,
    postedAt: PropTypes.instanceOf(Date),
    title: PropTypes.string
  })
};

function NewsItem({ news }) {
  const { image, title, description, postedAt } = news;

  return (
    <Stack direction="row" alignItems="center" spacing={2}>
      <Box component="img" alt={title} src={image} sx={{ width: 48, height: 48, borderRadius: 1.5 }} />
      <Box sx={{ minWidth: 240 }}>
        <Link to="#" color="inherit">
					<Typography variant="subtitle2" noWrap>
            {title}
          </Typography>
        </Link>
        <Typography variant="body2" sx={{ color: 'text.secondary' }} noWrap>
          {description}
        </Typography>
      </Box>
      <Typography variant="caption" sx={{ pr: 3, flexShrink: 0, color: 'text.secondary' }}>
        {fToNow(postedAt)}
      </Typography>
    </Stack>
  );
}

NewsComponent.propTypes = {
  contentTypeCode: PropTypes.string.isRequired,
  categoryCode: PropTypes.string.isRequired,
  serviceUrl: PropTypes.string,
};

const mapContentPayloadDTOToNews = (newsContent, language) => {
  return newsContent.map(content => {
    const news = {};
    news.id = content.id;
    news.postedAt = new Date(content.created);
    content.attributes.forEach(attribute => {
      switch (attribute.code) {
        case "title":
          news.title = attribute.values[language];
          break;
        case "desc":
          news.description = attribute.values[language];
          break;
        case "image":
          // TODO: remove localhost from here
          news.image = attribute.values[language].versions.find(el => el.dimensions === "150x150 px").path;
        default:
          break;
      }
    });
    return news;
  });
}

export default function NewsComponent({contentTypeCode, serviceUrl, categoryCode, widgetTitle}) {
  const [newsContent, setNewsContent] = useState([]);

  const { t, i18n } = useTranslation();

  useEffect(async () => {
    const response = await apiConferenceGet(serviceUrl, categoryCode, contentTypeCode);
    setNewsContent(response.payload);
  }, []);

  const reducedNews = mapContentPayloadDTOToNews(newsContent, i18n.language);

  return (
    <Card>
      <CardHeader title={widgetTitle || t("common.widgetName")} />

      <Scrollbar>
        <Stack spacing={3} sx={{ p: 3, pr: 0 }}>
          {reducedNews.map((news) => (
            <NewsItem key={news.id} news={news} />
          ))}
        </Stack>
      </Scrollbar>
    </Card>
  );
}
